





























import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import ContractRepository from '@/services/repositories/contract'
import getRepository from '@/services'
import DistrictMixin from '@/mixins/commonMixin'

@Component({ components: {} })
export default class PopupConfirmAuditing extends Mixins(PopupMixin, DistrictMixin) {
  @Prop({
    required: false,
    default: {
      type: '',
      code: ''
    }
  })
  data?: Record<string, any>
  apiContract?: ContractRepository

  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.districtId
    })
  }

  get totalAmount(): string {
    if (this.data?.totalAmount) {
      return this.$options.filters?.formatNumber(this.data.totalAmount)
    }
    return ''
  }

  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-confirm-auditing',
      isOpen: false
    })
  }
  async handleConfirm(): Promise<void> {
    try {
      const data =
        this.data?.type === 'LPB'
          ? {
              contractNo: this.data?.contractNo,
              applyFormNo: this.data?.applyFormNo
            }
          : this.data?.isNull
          ? {}
          : {
              contractNo: this.data?.contractNo,
              applyFormNo: this.data?.applyFormNo
            }
      const params = {
        paymentDiffId: this.data?.id,
        paymentSource: this.data?.type
      }
      if (this.data?.delete) {
        await this.apiContract?.deletePaymentDiff(this.data.id, { paymentSource: 'LPB' })
        this.$message.success('Xóa thành công')
        this.setOpenPopup({
          popupName: 'popup-confirm-auditing',
          isOpen: false
        })
        this.setOpenPopup({
          popupName: 'popup-detail-auditing',
          isOpen: false
        })
        this.$emit('reload')
      } else {
        await this.apiContract?.updatePaymentDiff(params, data, true)
        this.$emit('reload')
        this.setOpenPopup({
          popupName: 'popup-confirm-auditing',
          isOpen: false
        })
        this.$message.success('Cập nhật thông tin thành công')
      }
    } catch (error) {
      this.setOpenPopup({
        popupName: 'popup-confirm-auditing',
        isOpen: false
      })
      this.$message.error(error.message)
      console.log(error)
    }
  }
}
