@Component({ components: { CreateCost } })
export default class PopupDetailDiff extends Mixins(PopupMixin, DistrictMixin) {
  @Prop({
    required: false,
    default: {}
  })
  data?: Record<string, any>
  @Prop() dataEdit!: Record<string, any>
  @Prop({ required: true, default: 0 }) indexRow?: any

  apiContract?: ContractRepository

  isLoading = false
  isShowAction = true

  radioTrue = ''
  objDiff: Record<string, any> = {}

  dataRow: Record<string, any> = {}
  index = 0

  isEdit = false

  arrayRow: Array<Record<string, any>> = [
    {
      id: 1,
      title: 'Ngày phí phát sinh',
      lpbValue: 'payFeeDate',
      daiichiValue: 'payFeeDate1'
    },
    {
      id: 2,
      title: 'Số HSYCBH',
      lpbValue: 'applyFormNo',
      daiichiValue: 'applyFormNo1'
    },
    {
      id: 3,
      title: 'Số HĐ',
      lpbValue: 'contractNo',
      daiichiValue: 'contractNo1'
    },
    {
      id: 18,
      title: 'Ngày phát hành',
      lpbValue: 'issueDate',
      daiichiValue: 'issueDate1'
    },
    {
      id: 4,
      title: 'Thời hạn HĐ',
      lpbValue: 'numOfContractYear',
      daiichiValue: 'numOfContractYear1'
    },
    {
      id: 5,
      title: 'Thời hạn đóng phí',
      lpbValue: 'numOfFeeYear',
      daiichiValue: 'numOfFeeYear1'
    },
    {
      id: 6,
      title: 'Năm phí hiện tại',
      lpbValue: 'currentFeeYear',
      daiichiValue: 'currentFeeYear1'
    },
    {
      id: 17,
      title: 'Định kỳ đóng phí',
      lpbValue: 'paymentPeriod',
      daiichiValue: 'paymentPeriod1'
    },
    {
      id: 7,
      title: 'Mã ĐVKD',
      lpbValue: 'transactionOfficeCode',
      daiichiValue: 'transactionOfficeCode1'
    },
    {
      id: 15,
      title: 'Mã NVTV',
      lpbValue: 'salesMemberNo',
      daiichiValue: 'salesMemberNo1'
    },
    {
      id: 16,
      title: 'Tên NVTV',
      lpbValue: 'salesFullName',
      daiichiValue: 'salesFullName1'
    },
    {
      id: 8,
      title: 'Mã code',
      lpbValue: 'consultantNo',
      daiichiValue: 'consultantNo1'
    },
    {
      id: 9,
      title: 'NV đứng code',
      lpbValue: 'consultantName',
      lpbValueCode: 'consultantMemberNo',
      daiichiValueCode: 'consultantMemberNo1',
      daiichiValue: 'consultantName1'
    },
    {
      id: 10,
      title: 'Tên BMBH',
      lpbValue: 'customerFullName',
      daiichiValue: 'customerFullName1'
    },
    {
      id: 11,
      title: 'Mã SP',
      lpbValue: 'productCode',
      daiichiValue: 'productCode1'
    },
    {
      id: 12,
      title: 'Phí cơ bản',
      lpbValue: 'basicAmount',
      daiichiValue: 'basicAmount1'
    },
    {
      id: 13,
      title: 'Phí đóng thêm',
      lpbValue: 'extraAmount',
      daiichiValue: 'extraAmount1'
    },
    {
      id: 14,
      title: 'Tổng phí',
      lpbValue: 'totalAmount',
      daiichiValue: 'totalAmount1'
    }
  ]
  propertyLpb = [
    'payFeeDate',
    'applyFormNo',
    'contractNo',
    'issueDate',
    'numOfContractYear',
    'numOfFeeYear',
    'currentFeeYear',
    'salesMemberNo',
    'salesFullName',
    'transactionOfficeCode',
    'consultantNo',
    'consultantName',
    'customerFullName',
    'productCode',
    'basicAmount',
    'extraAmount',
    'totalAmount'
  ]
  propertyDaiichi = [
    'payFeeDate1',
    'applyFormNo1',
    'contractNo1',
    'issueDate',
    'numOfContractYear1',
    'numOfFeeYear1',
    'currentFeeYear1',
    'salesMemberNo1',
    'salesFullName1',
    'transactionOfficeCode1',
    'consultantNo1',
    'consultantName1',
    'customerFullName1',
    'productCode1',
    'basicAmount1',
    'extraAmount1',
    'totalAmount1'
  ]

  isDisableLpb = false

  isNullDaiichi = false

  get getRole(): boolean {
    return this.checkPemission('audit', ['edit-auditing'])
  }
  get getDisableNext(): boolean {
    return this.index === (this.data && this.data?.length - 1)
  }
  get getDisablePrev(): boolean {
    return this.index === 0
  }

  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.districtId
    })
  }

  handleEdit(): void {
    let code = 'edit-auditing'
    this.$emit('openEdit', { code, data: this.dataRow })
    setTimeout(() => {
      this.setOpenPopup({
        popupName: 'popup-detail-auditing',
        isOpen: false
      })
    }, 1000)
  }
  handleAddCost(): void {
    let code = 'add'
    this.$emit('openEdit', { code, data: this.dataRow })
    setTimeout(() => {
      this.setOpenPopup({
        popupName: 'popup-detail-auditing',
        isOpen: false
      })
    }, 1000)
  }

  handleDelete(): void {
    const data = {
      delete: true,
      applyFormNo: this.dataRow.applyFormNo,
      id: this.dataRow.id,
      contractNo: this.dataRow.contractNo,
      totalAmount: this.dataRow.totalAmount,
      productCode: this.dataRow.productCode
    }
    this.$emit('delete', { code: 'delete', data })
  }

  handleNextData(): void {
    this.isLoading = true
    this.index += 1
    if (this.index === this.data?.length) {
      this.setOpenPopup({
        popupName: 'popup-detail-auditing',
        isOpen: false
      })
      return
    }
    //@ts-ignore
    this.dataRow = this.data[this.index]
    this.objDiff = {}
    this.handleCompareDiff()
    this.isLoading = false
  }
  handlePrevData(): void {
    this.isLoading = true
    this.index -= 1
    //@ts-ignore
    this.dataRow = this.data[this.index]
    this.objDiff = {}
    this.handleCompareDiff()
    this.isLoading = false
  }

  async handleSaveClose(): Promise<void> {
    try {
      if (this.radioTrue) {
        const data =
          this.radioTrue === 'LPB'
            ? {
                contractNo: this.dataRow.contractNo,
                applyFormNo: this.dataRow.applyFormNo
              }
            : this.isNullDaiichi
            ? {}
            : {
                contractNo: this.dataRow.contractNo1,
                applyFormNo: this.dataRow.applyFormNo1
              }
        const params = {
          paymentDiffId: this.dataRow.id,
          paymentSource: this.radioTrue
        }
        await this.apiContract?.updatePaymentDiff(params, data, true)
        this.$emit('reload')
        this.setOpenPopup({
          popupName: 'popup-detail-auditing',
          isOpen: false
        })
        this.$message.success('Cập nhật thông tin thành công')
      } else {
        this.$message.error('Cần chọn dữ liệu đúng để đối soát')
      }
    } catch (error) {
      this.$message.error(error.message)
    }
  }
  async handleSaveNext(): Promise<void> {
    try {
      if (this.radioTrue) {
        const data =
          this.radioTrue === 'LPB'
            ? {
                contractNo: this.dataRow.contractNo,
                applyFormNo: this.dataRow.applyFormNo
              }
            : {
                contractNo: this.dataRow.contractNo1,
                applyFormNo: this.dataRow.applyFormNo1
              }
        const params = {
          paymentDiffId: this.dataRow.id,
          paymentSource: this.radioTrue
        }
        await this.apiContract?.updatePaymentDiff(params, data, true)
        this.$message.success('Cập nhật thông tin thành công')

        this.handleNextData()

        this.isEdit = true
      } else {
        this.$message.error('Cần chọn dữ liệu đúng để đối soát')
      }
    } catch (error) {
      this.$message.error(error.message)
    }
  }

  handleOpen(): void {
    this.objDiff = {}
    this.isEdit = false
    this.radioTrue = 'LPB'
    this.isLoading = true
    this.index = this.indexRow
    //@ts-ignore
    this.dataRow = this.data?.length && this.data[this.index]
    this.handleCompareDiff()
    this.isLoading = false
  }

  handleClose(): void {
    this.isDisableLpb = false
    if (this.isEdit) {
      this.$emit('reload')
    }
    this.setOpenPopup({
      popupName: 'popup-detail-auditing',
      isOpen: false
    })
  }

  convertPaymentPeriod(): void {
    if (this.dataRow.paymentPeriod === 1) {
      this.dataRow.paymentPeriod = 'Năm'
    }
    if (this.dataRow.paymentPeriod === 2) {
      this.dataRow.paymentPeriod = 'Nửa năm'
    }
    if (this.dataRow.paymentPeriod === 4) {
      this.dataRow.paymentPeriod = 'Quý'
    }
    if (this.dataRow.paymentPeriod === 12) {
      this.dataRow.paymentPeriod = 'Tháng'
    }
    if (this.dataRow.paymentPeriod === 0) {
      this.dataRow.paymentPeriod = '1 lần'
    }
    if (this.dataRow.paymentPeriod1 === 1) {
      this.dataRow.paymentPeriod1 = 'Năm'
    }
    if (this.dataRow.paymentPeriod1 === 2) {
      this.dataRow.paymentPeriod1 = 'Nửa năm'
    }
    if (this.dataRow.paymentPeriod1 === 4) {
      this.dataRow.paymentPeriod1 = 'Quý'
    }
    if (this.dataRow.paymentPeriod1 === 12) {
      this.dataRow.paymentPeriod = 'Tháng'
    }
    if (this.dataRow.paymentPeriod1 === 0) {
      this.dataRow.paymentPeriod = '1 lần'
    }
  }

  handleCompareDiff(): void {
    this.convertPaymentPeriod()
    let isNullLpb = false
    let isNullDaiichi = false
    this.isDisableLpb = false
    for (let i = 0; i < this.propertyLpb.length; i++) {
      //@ts-ignore
      if (this.dataRow[this.propertyLpb[i]]) {
        isNullLpb = false
        break
      } else {
        isNullLpb = true
      }
    }

    for (let i = 0; i < this.propertyDaiichi.length; i++) {
      //@ts-ignore
      if (this.dataRow[this.propertyDaiichi[i]]) {
        isNullDaiichi = false
        break
      } else {
        isNullDaiichi = true
      }
    }

    if (isNullLpb && !isNullDaiichi) {
      this.isShowAction = false
      this.radioTrue = ''
    }
    if (!isNullLpb && isNullDaiichi) {
      this.isShowAction = true
      this.radioTrue = ''
      this.isDisableLpb = true
    }

    if (!isNullLpb && !isNullDaiichi) {
      this.isShowAction = true
      this.radioTrue = ''
      forEach(this.arrayRow, row => {
        //@ts-ignore
        if (this.dataRow[row.lpbValue] !== this.dataRow[row.daiichiValue]) {
          this.objDiff[row.id] = true
        } else {
          this.objDiff[row.id] = false
        }
      })
    }
    this.isNullDaiichi = isNullDaiichi
  }
}
