  handleRowClick(row: Record<string, any>): void {
    console.log(row)
    this.indexRow = findIndex(this.dataTable, { id: row.row.id })

    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }
    if (this.checkReqToEdit == true) {
      return
    }
    this.contractInfor = row.row
    this.dataPopup.action = 'detail'
    this.dataPopup.data = [row.row]
    this.dataPopup.total = this.dataPopup.data.length
    this.dataRow = row.row
    this.setOpenPopup({
      popupName: 'popup-detail-auditing',
      isOpen: true
    })
  }

  handlePrevPopupAudit(): void {
    if (this.params.page > 1) {
      this.params.page--
      this.apiContract
        ?.getData(this.params)
        .then(list => {
          const key = 'bankContractNo'
          const listFilter = [...new Map(list.data.map((item: Record<string, any>) => [item[key], item])).values()]
          this.dataPopup.count = listFilter.length - 1
          this.dataPopup.data = [...listFilter, ...this.dataPopup.data]
        })
        .catch(err => this.$message.error(err.message))
    }
  }
  handleNextPopupAudit(): void {
    this.params.page++
    this.apiContract
      ?.getData(this.params)
      .then(list => {
        const key = 'bankContractNo'
        const listAll = [...this.dataPopup.data, ...list.data]
        const listFilter = [...new Map(listAll.map((item: Record<string, any>) => [item[key], item])).values()]
        this.dataPopup.data = listFilter
      })
      .catch(err => this.$message.error(err.message))
  }
