          popupName: 'edit-popup',
          isOpen: true
        })
        break
      case 'yeu-cau-hieu-chinh':
        this.setOpenPopup({
          popupName: 'cancel-popup',
          isOpen: true
        })
        break
      case undefined:
        this.dataPopupAction.title = 'Gán nhân viên tư vấn'
        this.dataPopupAction.label = 'Nhân viên tư vấn'
        this.setOpenPopup({
          popupName: 'audit-action',
