  handleSelection(value: Array<Record<string, any>>, row: Record<string, any>): void {
    this.pageSize = this.params.limit

    if (row) {
      const checked = value.find(item => item.id == row.id)
      if (checked && checked.id > 0) {
        let selection = this.dataTable.filter((item: any) => item.bankContractNo === row?.bankContractNo && item.id !== row?.id)
        //@ts-ignore
        this.$refs.auditTable.updateSelection(selection)
        //@ts-ignore
        this.dataSelect = this.$refs.auditTable.getSelection()
      } else {
        this.dataSelect = value.filter((item: any) => item.bankContractNo !== row?.bankContractNo)
        //@ts-ignore
        this.$refs.auditTable.clearSelection()
        //@ts-ignore
        this.$refs.auditTable.updateSelection(this.dataSelect)
      }
      const lastBankContractNo = this.dataTable[this.dataTable.length - 1].bankContractNo
      const checkLast = this.dataSelect.some(item => item.bankContractNo == lastBankContractNo)
      if (checkLast) {
        this.checkLastItemSelect = true
      } else {
        this.checkLastItemSelect = false
      }
    } else {
      this.dataSelect = value
    }
    // let a = this.listFilter.filter((item: any) => item.customerType === '1' || item.customerType === '4')
    // if(a.length >0){
    //   this.checkCustomerType =true
    // }
  }
