  checkDataRoute(value: string): any {
    //@ts-ignore
    // const value = this.$route.name
    if (value === 'new') {
      return 1
    }
    if (value === 'cancel') {
      return 2
    }
    if (value === 'effective') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'contract') {
      return ''
    }
    if (value === 'pending') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'refuse') {
      return 4
    }
  }
  url = ''
  async handleOpenExportFile(): Promise<void> {
    if (this.dataTable.length < 1) {
      this.$message.warning('Không tìm thấy dữ liệu xuất file')
      return
    }
    if (this.query.total >= 50000) {
      this.$message.warning('Số lượng bản ghi vượt quá 50000, vui lòng sử dụng bộ lọc')
      return
    }
    this.isLoadingButton = true
    let response: any
    const date = new Date()
    const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}_${date.getHours()}${date.getMinutes()}${date.getSeconds()}`
    delete this.params.newAuditStatus
    await this.apiContract
      ?.exportAuditingExcel(this.params, this.districtId)
      .then(data => {
        response = data
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', `Danh_sach_doi_soat_${time}.xlsx`)
        document.body.appendChild(link)
        link.click()
        this.isLoadingButton = false
      })
      .catch(() => {
        this.isLoadingButton = false
      })
  }

  handleSendEmail(): void {
    this.isLoadingEmail = true
    this.apiContract
      ?.sendEmail(this.params)
      .then(() => {
        this.$message.success('Gửi mail thành công')
        this.isLoadingEmail = false
      })
      .catch(error => {
        this.isLoadingEmail = false
        this.$message.error(error.message)
      })
  }

  handleCheckLpbNull(row: Record<string, any>): boolean {
    let isNullLpb = false
    for (let i = 0; i < this.propertyLpb.length; i++) {
      //@ts-ignore
      if (row[this.propertyLpb[i]]) {
        isNullLpb = false
        break
      } else {
        isNullLpb = true
      }
    }
    return isNullLpb
  }

  handleCheckDaichiNull(row: Record<string, any>): boolean {
    let isNullDaichi = false
    for (let i = 0; i < this.propertyDaichi.length; i++) {
      //@ts-ignore
      if (row[this.propertyDaichi[i]]) {
        isNullDaichi = false
        break
      } else {
        isNullDaichi = true
      }
    }
    console.log('isNullDaichi', isNullDaichi)

    return isNullDaichi
  }

  async init(): Promise<void> {
    this.isLoading = true
    this.dataSelect = []
    this.dataPopup.action = ''
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.params = assign(this.params, filters.filters)
    }
    if (!this.params.page) {
      this.params.page = 1
    }
    if (!this.params.limit) {
      this.params.limit = 20
    }
    if (!this.params.orderByClause) {
      this.params.orderByClause = '1'
    }
    this.query.page = this.params.page
    this.query.limit = this.params.limit
    delete this.params.newAuditStatus

    await this.apiContract
      ?.getData(this.params)
      .then((list: Record<string, any>) => {
        this.data = list
        this.dataTable = this.data.data
        this.isLoading = false
      })
      .catch(() => {
        this.dataTable = []
        this.isLoading = false
      })
    this.apiContract
      ?.getContractCount(this.params)
      .then(count => {
        this.query.total = count.data
        this.isLoading = false
      })
      .catch(() => {
        this.query.total = 0
        this.isLoading = false
      })
  }

  setParamsForUser(): void {
    if (this.getDisableArea) {
      this.params.areaIdArray = `${this.user?.areaId}`
      this.params.branchIdArray = `${this.user?.branchId}`
    }
    if (this.getDisableTransaction) {
      this.params.transOfficeIdArray = `${this.user?.transactionOfficeId}`
    }
    if (this.getDisableSaleMember) {
      this.params.salesMemberIdArray = `${this.user?.id}`
    }
  }

  setData(): void {
    this.apiParam?.getParamRelationship().then(list => (this.listType = list))
    // this.setParamsForUser()
    this.init()
  }

  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    this.setData()
  }

  beforeDestroy(): void {
    //@ts-ignore
    clearInterval(this.callAction)
  }

  handleDeleteDetail(param: { code: string; data: Record<string, any> }): void {
    this.detailConfirm = param.data
    this.setOpenPopup({
      popupName: 'popup-confirm-auditing',
      isOpen: true
    })
  }

  handleActionAudit(param: { code: string; data: Record<string, any> }): void {
    const { code, data } = param
    if (code === 'delete') {
      this.detailConfirm = {
        delete: true,
        applyFormNo: data.applyFormNo,
        id: data.id,
        contractNo: data.contractNo,
        totalAmount: data.totalAmount,
        productCode: data.productCode
      }
      this.setOpenPopup({
        popupName: 'popup-confirm-auditing',
        isOpen: true
      })
    } else if (code === 'edit-auditing' || code === 'add') {
      if (code == 'add') {
        this.dataEdit.title = 'Thêm mới thu phí'
      } else {
        this.dataEdit.title = 'Sửa thu phí'
      }
      this.dataEdit.code = code
      this.dataEdit.value = data
      this.dataEdit.type = 'audit'
      this.setOpenPopup({
        popupName: 'create-cost',
        isOpen: true
      })
    }
  }

  handleUpdatePage(): void {
    this.init()
  }

  handleReload(): void {
    this.init()
  }
  summaryMethod(params: { columns: any; data: any[] }): any[] {
    const { columns } = params
    const sums: any[] = []

    forEach(columns, (column, index: number) => {
      if (index === 4) {
        sums[index] = 'DỮ LIỆU LPB'
        return
      }
      if (index === 10) {
        sums[index] = 'DỮ LIỆU Dai-ichi'
        return
      }
      sums[index] = ''
    })
    return sums
  }

  handleCompareValue(rowIndex: number, column: Record<string, any>): boolean {
    let valueLpb = this.dataTable[rowIndex][column.property]
    let valueDaichi = this.dataTable[rowIndex][column.property + '1']
    return valueLpb !== valueDaichi
  }

  handleCompareValueRight(rowIndex: number, column: Record<string, any>): boolean {
    let valueDaichi = this.dataTable[rowIndex][column.property]
    const propertyLpb = column.property.replace('1', '')
    let valueLpb = this.dataTable[rowIndex][propertyLpb]
    return valueDaichi !== valueLpb
  }

  cellClassName(params: { row: any; column: any; rowIndex: any; columnIndex: any }): string {
    const { rowIndex, row, column, columnIndex } = params
    if (columnIndex !== 0 && columnIndex !== 6 && columnIndex !== 12) {
      if (columnIndex <= 5) {
        if (!row.totalAmount && !row.currentFeeYear && !row.productCode && !row.applyFormNo && !row.transactionOfficeCode) {
          return 'no-data'
        }
        const rs = this.handleCompareValue(rowIndex, column)
        if (rs) {
          return 'diff'
        }
      }
      if (columnIndex >= 7 && columnIndex <= 11) {
        if (!row.totalAmount1 && !row.currentFeeYear1 && !row.productCode1 && !row.applyFormNo1 && !row.transactionOfficeCode1) {
          return ''
        }
        const rs = this.handleCompareValueRight(rowIndex, column)
        if (rs) {
          return 'diff'
        }
      }
    }
    return ''
  }
  handleClickCbx(row: Record<string, any>, type: string): void {
    this.detailConfirm = {
      type,
      applyFormNo: type === 'LPB' ? row.applyFormNo : row.applyFormNo1,
      id: row.id,
      contractNo: type === 'LPB' ? row.contractNo : row.contractNo1,
      totalAmount: type === 'LPB' ? row.totalAmount : row.totalAmount1,
      productCode: type === 'LPB' ? row.productCode : row.productCode1,
      isNull: this.handleCheckDaichiNull(row)
    }
    this.setOpenPopup({
      popupName: 'popup-confirm-auditing',
      isOpen: true
    })
  }

  handleSetInterval(): void {
    //@ts-ignore
    this.callAction = setInterval(() => {
      this.handleGetStatusLog()
    }, 2000)
  }

  async handleAudit(): Promise<void> {
    try {
      const rs = await this.apiContract?.createAuditLog()
      this.$message.success('Hệ thống đang thực hiện đối soát. Vui lòng xem kết quả sau ít phút nữa')
      this.setAuditLog(rs)
      this.handleSetInterval()
    } catch (error) {
      this.$message.error(error.message)
      console.log(error)
    }
  }
  async handleGetStatusLog(): Promise<void> {
    try {
      const rs = await this.apiContract?.getStatusLog(this.auditLog.id)
      if (rs.status === 'COMPLETED') {
        //@ts-ignore
        clearInterval(this.callAction)
        this.init()
      }
    } catch (error) {
      console.log(error)
    }
  }
}
